import React, {useState, useEffect} from "react";
import Swiper from "swiper";

function Recommend(props) {

  function vwToPx(vw) {
    return window.innerWidth / 100 * vw
  }

  useEffect(() => {
    const swiper = new Swiper('.swiper-container', {
      // slidesPerView: 1,
      // centeredSlides: true,
      width: vwToPx(51.3),
      // loop: true,
      // 如果需要分页器
      pagination: {
        el: '.swiper-pagination',
      },
      // observer:true,//修改swiper自己或子元素时，自动初始化swiper
      // observeParents:true,
      // autoplay: true,
      initialSlide: 0,
      spaceBetween: vwToPx(5.3),
      on: {
        slideChangeTransitionEnd: function () {
        }
      }
    })
  }, [props]);
  return (
    <div className="recommend">
      <i className='layout-title'></i>
      <div className="swiper-container">
        <div className="swiper-wrapper">
          {props.list.map((item, index) => {
            //判断当前顶部是哪个id
            if (props.id && item.id === props.id) {
              return
            } else {
              return (
                <div className={"swiper-slide swiper-slide" + index}>
                  <img src={item.img}
                       className={`icon-${index + 1}`}></img>
                  <p className='recommend-title'> {item.title}</p>
                  <p className='recommend-text'>
                    {item.text}
                  </p>
                </div>
              )
            }
          })
          }
        </div>
      </div>
    </div>
  );
}

export default Recommend;
