import React, {Component} from "react";
import "./index.scss";
import MobileHeader from "../../../components/mobileHeader";
import MobileFooter from '../../../components/mobileFooter';
import {Tabs} from 'antd';
import $ from "jquery";

const {TabPane} = Tabs;
export default class MobileInduDeatail extends Component {
    state = {
        contentBoxList: [
            {
                text: '1、与开为科技进行联合研发，开为科技负责前端机器的安装以及数据收集，全天智能负责对数据的整合展示。项目专注于新零售领域数据可视化以及分析，通过对零售门店日常数据的汇总、可视化、分析，最终将成果应用于精准营销、供应链管理、企业运营等领域。',
                img: require('../../../assets/img/industry/detail/1@3x-min.png')
            },
            {
                text: '2、开为科技与沃尔玛超市形成商业合作，以水榭春天店为基础展开，作为开为的数据可视化合作伙伴，全天与开为基于商业关系，承担此次项目的数据可视化部分，满足沃尔玛对于数据呈现的需求，聚焦关键数据指标，达到优化商场运营效率，提高商品转化率的目的。',
                img: require('../../../assets/img/industry/detail/2@3x-min.png')
            }
        ],
        titleKey:['方案介绍','行业痛点','方案价值']
    };

    render() {
        return (
            <div className="mobile-industry-detail-wrapper">
                <MobileHeader/>
                <div className="plan-header-11">
                </div>
                <div className="induDeatail-wrapper">
                    <div className="logo-header">
                        <span>智慧零售</span>
                    </div>
                    <div className="induDeatail">
                        <div className="left-section">
                            <main>
                                <Tabs animated={false} defaultActiveKey="1">
                                    <TabPane tab="方案介绍"
                                        key="1">
                                        <div className="key1">
                                            <p>通过网络数据技术更精准的掌握消费者的消费行为和特征，洞察客户需求进行消费服务引导，对各类客户进行统计、分类，增强客户粘性，提高消费频率和消费额度。</p>
                                            <div className="key1-img"></div>
                                        </div>
                                    </TabPane>
                                    <TabPane tab="行业痛点"
                                        key="2">
                                        <div className="key2">
                                            <p>1、线上线下数据鸿沟，线下商家与电商之间的营销差异和数据差异</p>
                                            <p>2、获客难，流量成本越来越高，如何找到更新的盈利模式，如何找到性价比高的流量来源</p>
                                            <p>3、无法做到场景的精细化运营来节省零售业运营成本、有效提升经营效率、规模化提升零售营业额</p>
                                            <p>4、无法快速定位市场的下沉空间、以及对核心商圈和交通枢纽的整体布局</p>
                                        </div>
                                    </TabPane>
                                    <TabPane tab="方案价值"
                                        key="3">
                                        <div className="key3">
                                            <p>
                                                利用5G技术和物联网技术，把没有确定性的客户锁定成私域流量，让营销更有主动性，更智能的分析高频高额消费区域，分析竞争业态，在面对消费者市场，竞业市场起到更科学动态的分析，并以全可视化的数字呈现在终端，为企业主决策提供更科学精确的分析数据。可以建立细分终端，实时和消费者互动，采集数据，分析数据，对不同的客户进行跟进服务，使企业能更好的服务存量客户，拓展新客户，建立稳定健康持续发展的生态系统。
                                            </p>
                                        </div>
                                    </TabPane>
                                    <TabPane tab="产品优势"
                                        key="4">
                                        <div className="key4">
                                            <div className="par">
                                                <h1>1、运用O2O降低成本</h1>
                                                <p>打通线上线下库存体系，把消费数据连接起来，构成一个随时在线的O2O平台。</p>
                                            </div>
                                            <div className="par">
                                                <h1>2、精准会员营销和品牌会员营销</h1>
                                                <p>降低营销成本，提高顾客的复购率，增加顾客的购物黏性。通过建立后台追销系统，延长产品价值链，提升顾客的重复购买率。分析顾客购买习惯以及人群大数据分析，同步进客户数据库，不断挖掘顾客的后端价值。</p>
                                            </div>
                                            <div className="par">
                                                <h1>3、快速开店跑马圈地布局</h1>
                                                <p>运用大数据分析发现市场覆盖率，开发地区空白市场，探究地区下沉空间。通过统计分析总人口增长、地区人口流入、地区人口消费习惯，预估市场门店投放总数及地区性投放建议。</p>
                                            </div>
                                            <div className="par">
                                                <h1>4、渠道管理支撑门店扩张</h1>
                                                <p>完善各级销售网络、渠道供应商、线上线下门店的全方位数据供应链体系。进行SAP信息化系统建设，不断提高产品质量。</p>
                                            </div>
                                        </div>
                                    </TabPane>
                                    <TabPane tab="客户案例"
                                        key="5">
                                        <div className="key5">
                                            {
                                                this.state.contentBoxList.map((item, index) => (
                                                        <div className="contentBoxList"
                                                            key={index}>
                                                            <p>{item.text}</p>
                                                            <div className="gif">
                                                                <img src={item.img}
                                                                    alt=""/>
                                                            </div>
                                                        </div>
                                                    )
                                                )
                                            }
                                        </div>
                                    </TabPane>
                                </Tabs>
                            </main>
                        </div>
                        <div className="right-section">
                            <h1>相关应用推荐</h1>
                            <div className="recommend-logo">
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-1.png")}
                                        alt=""/>
                                </div>
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-2.png")}
                                        alt=""/>
                                </div>
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-3.png")}
                                        alt=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <MobileFooter/>
            </div>
        );
    }
}
