import React from "react";
import './index.scss'
import HeadList from "./components/5g-overView";
import {Link} from 'react-router-dom';
import $ from 'jquery'

function Header() {
    const [isMenuShow,setIsMenuShow] = React.useState(false);
    const menuShow = (bol,e)=>{
        e.stopPropagation();
        setIsMenuShow(bol);
        if(bol) {
            document.body.style = 'height:100vh;overflow:hidden';
            Array.from( $('.blur-wrapper').children()).forEach((item,index)=>{
                if($(item)[0].className !== 'home-nav') {
                    $(item).addClass('blur');
                }
            })
        }else {
            Array.from( $('.blur-wrapper').children()).forEach((item,index)=>{
                if($(item)[0].className !== 'home-nav') {
                    $(item).removeClass('blur');
                }
            });
            document.body.style = '';
        }
    };
    const handleCancel = (e)=>{
        menuShow(true,e)
    };
    return (
        <div className='home-nav' onClick={(e)=>menuShow(false,e)}>
            <Link to="/">
                <i className='home-nav-logo'></i>
            </Link>
            <i onClick={(e)=>menuShow(!isMenuShow,e)} className='home-nav-list'></i>
            {
                isMenuShow ?  <HeadList onCancel={handleCancel} ></HeadList> : null
            }
        </div>
    )
}

export default Header;
