import React, {Component, useState} from "react";
import "./index.scss";
import MobileHeader from "../../components/mobileHeader";
import MobileFooter from '.././../components/mobileFooter';
import FooterList from "./components/footerList";

export default class MobileDeveFile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow: true,
    };
    // 这个绑定是必要的，使`this`在回调中起作用
    this.handleMore = this.handleMore.bind(this);
    this.handleHide = this.handleHide.bind(this);
  }

  render() {
    return (
      <div className="blur-wrapper mobile-deve">
        <MobileHeader/>
        <div className="mobile-face-box"></div>
        <div className="mobile-bg-wrapper">
          <div className="mobile-guide">
            <div>
              <h2 className="user-icon"></h2>
              <div className="mobile-guide-container">
                <div className="mobile-guide-container-box err-disable ">
                </div>
                <div className="guide-bg-2 err-disable ">
                </div>
                <div className="guide-bg-3 no-margin err-disable">
                </div>
              </div>
            </div>
          </div>
          <div className="m-secDe sec-deveFile">
            <div >
              <h2 className="secDe-tit"></h2>
              <FooterList/>
            </div>
          </div>
        </div>
        <MobileFooter/>
      </div>
    );
  }

  handleMore(event) {
    // event.stopPropagation();
    this.setState({
      isShow: false,
    });
  }

  handleHide() {
    this.setState({
      isShow: true,
    });
  }
}
