import React, { useState } from "react";
import {Menu} from "antd";
function Scene(props) {
    const [current, setCurrent] = useState("1");
    /*const handleClick = (e) => {
        console.log(e)
        setCurrent(e.key);
    };*/
  return (
      <div className="scene">
              <i className='layout-title'></i>
              <div className="scene-list">
                  <Menu
                      mode="inline"
                      selectedKeys={[current]}
                      defaultSelectedKeys="1"
                  >
                      {
                          props.list.Menu.map((item,index)=>(
                              <Menu.Item key={index+1} onMouseOver={()=> setCurrent(String(index+1))}><i className='menu-key'>{'0'+(index+1)}</i><span>{item}</span><i className='menu-line'></i></Menu.Item>
                          ))
                      }
                  </Menu>
                  <div className="scene-container">
                      <div className="scene-container-top">
                          <div className="top-box">
                              {props.list.contentBoxList[current - 1].title}
                              <p>
                                  {props.list.contentBoxList[current - 1].text}
                              </p>
                          </div>

                      </div>
                      <div className="demo-list">
                          <div className='demo-line'></div>
                          <ul className="demo-list-ul">
                              {props.list.logoImg.length>0?(  <li>
                                  <p className="img-name">合作案例</p>
                              </li>):''}

                              {
                                  props.list.logoImg.map((item,index)=>(
                                      <li className={"demo-list-item item-"+item} key={index}></li>
                                  ))
                              }

                              {/*<li className="demo-list-item item-2"></li>*/}
                              {/*<li className="demo-list-item item-3"></li>*/}
                          </ul>
                      </div>
                  </div>
              </div>
          </div>
  );
}

export default Scene;
