import React, {Suspense, lazy, useEffect} from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import "./App.scss";
import "../src/assets/css/global.scss";
// import "./assets/js/common";
import "animate.css";
import Home from "./pages/home";
import MobileHome from './mobile/home'
import MobileOverView from './mobile/overView'
import OverView from "./pages/overview";
import Application from "./pages/application";
import MobileApplication from './mobile/application'
import DeveFile from "./pages/devefile"
import MobileDeveFile from './mobile/deveFile'
import FileDetail from "./pages/devefile/detail";
import MobileFileDetail from './mobile/deveFile/detail'
import Lndustry from "./pages/industry";
import MobileIndustry from './mobile/industry'
import InduDeatail from "./pages/industry/detail";
import MobileInduDeatail from './mobile/industry/detail'
import Ecology from "./pages/ecology";
import MobileEcology from './mobile/ecology'
import FourAssis from "./pages/fourassis";
import CityPlan from "./pages/cityplan";
import Consultation from "./pages/consultation";
import OnlineDay from './pages/onlineDay';
import Digital from './pages/digital';
import Article from './pages/article';
import MobileConsultation from './mobile/consultation'
// import JoinUs from "./pages/joinus";
import Price from "./pages/price";
import Ability from "./pages/ability";
import MobileAbility from './mobile/ability'
import Login from "./pages/wxInfo/loginInfo";
import testSMS from "./components/updateUser/testSMS";
// import MnInfo from "./components/updateUser/loginMnInfo";
import Scan from "./pages/login/scan";
import Jump from "./components/updateUser/Jump";
import Avatar from "./components/updateUser/Upload";
import Admin from "./pages/admin";
import Asstetation from "./pages/incorporate/incorporate";
import { IEVersion } from "./assets/libs/util";
IEVersion();

class ScrollToTop extends React.Component {
    componentDidUpdate(prevProps) {
        if (this.props.location !== prevProps.location && this.props.location.pathname !== '/industry') {
            window.scrollTo(0, 0)
        }
    }

    render() {
        return this.props.children
    }
}
