function App() {
    const [mobile, setMobile] = React.useState(null)

    useEffect(() => {
        if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
            setMobile(false);
        } else {
            setMobile(true);
        }
    });

    return (
        <div className="App">
            <Router>
                <Suspense fallback={<div>Loading...</div>}>
                    <Switch>
                        <ScrollToTop>
                            <Route exact
                                path="/">
                                {
                                    mobile ? (
                                        <Home></Home>
                                    ) : (
                                        <MobileHome></MobileHome>
                                    )
                                }
                            </Route>
                            <Route path="/overView">
                                {
                                    mobile ? (
                                        <OverView></OverView>
                                    ) : (
                                        <MobileOverView></MobileOverView>
                                    )
                                }
                            </Route>
                            <Route path="/ability/:id">
                                {
                                    mobile ? (
                                        <Ability></Ability>
                                    ) : (
                                        <MobileAbility></MobileAbility>
                                    )
                                }
                            </Route>
                            <Route exact
                                path="/application">
                                {
                                    mobile ? (
                                        <Application></Application>
                                    ) : (
                                        <MobileApplication></MobileApplication>
                                    )
                                }
                            </Route>
                            <Route path="/application/:id">
                                {
                                    mobile ? (
                                        <Application></Application>
                                    ) : (
                                        <MobileApplication></MobileApplication>
                                    )
                                }
                            </Route>
                            <Route path="/industry">
                                {
                                    mobile ? (
                                        <Lndustry></Lndustry>
                                    ) : (
                                        <MobileIndustry></MobileIndustry>
                                    )
                                }
                            </Route>
                            <Route path="/detail">
                                {
                                    mobile ? (
                                        <InduDeatail></InduDeatail>
                                    ) : (
                                        <MobileInduDeatail></MobileInduDeatail>
                                    )
                                }
                            </Route>
                            <Route path="/deveFile">
                                {
                                    mobile ? (
                                        <DeveFile></DeveFile>
                                    ) : (
                                        <MobileDeveFile></MobileDeveFile>
                                    )
                                }
                            </Route>
                            <Route path="/fileDetail/:id">
                                {
                                    mobile ? (
                                        <FileDetail></FileDetail>
                                    ) : (
                                        <MobileFileDetail></MobileFileDetail>
                                    )
                                }
                            </Route>
                            <Route path="/ecology">
                                {
                                    mobile ? (
                                        <Ecology></Ecology>
                                    ) : (
                                        <MobileEcology></MobileEcology>
                                    )
                                }
                            </Route>
                            <Route path="/fourassis">
                                <FourAssis></FourAssis>
                            </Route>
                            <Route path="/ctiyPlan">
                                <CityPlan></CityPlan>
                            </Route>
                            <Route path="/consultation">
                                {
                                    mobile ? (
                                        <Consultation></Consultation>
                                    ) : (
                                        <MobileConsultation></MobileConsultation>
                                    )
                                }
                            </Route>
                            <Route path="/onlineDay">
                                <OnlineDay></OnlineDay>
                            </Route>
                            <Route path="/digital">
                                <Digital></Digital>
                            </Route>
                            <Route path="/article">
                                <Article></Article>
                            </Route>

                            <Route path="/testSMS"
                                component={testSMS}/>
                            {/* <Route path="/contact" component={JoinUs} /> */}
                            <Route path="/price"
                                component={Price}/>
                            <Route path="/info"
                                component={() => <Login qrCodeLogin/>}/>
                            {/* <Route path="/mnInfo" component={() => <MnInfo qrCodeLogin />} /> */}
                            <Route path="/login"
                                component={() => <Scan/>}/>
                            <Route path="/incorporate" component={Asstetation} />
                            {/* <Route path="/admin"
                                component={Admin}/> */}
                            <Route path="/wxcodeJump"
                                component={Jump}/>
                            <Route path="/Upload"
                                component={Avatar}/>
                        </ScrollToTop>
                    </Switch>
                </Suspense>
            </Router>
        </div>
    );
}

export default App;
