class Article extends Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 1,
      pageSize: 4,
      articleArr: []
    }
  }

  componentDidMount() {
    const { pageSize } = this.state;
    let arr = [];
    for (let i = 0; i < pageSize; i ++) {
      arr.push(articles[i]);
    }
    this.setState({
      articleArr: arr
    });
  }

  _onPaginationEvent(page, pageSize) {
    let arr = [];
    for (let i = (page - 1) * pageSize; i < pageSize * page; i ++) {
      arr.push(articles[i]);
    }

    this.setState({
      articleArr: arr
    });
  }


  render() {
    const { articleArr } = this.state;
    return (
      <div>
        <Header />

        <div className="article">
          <div className="content">
            <div className="bread-crumb">
              <span>最新资讯</span>
              <span>{">"}</span>
              <span>全部文章</span>
            </div>
            <div className="item-content">
              {
                articleArr.map((item, index) => (
                  <div className="item">
                    <img src={item.imgSrc} />
                    <div className="item-info">
                      <p className="item-title">
                        <span>{item.title}</span>
                        <span className="item-time">{item.time}</span>
                      </p>
                      <p className="item-content">{item.content}</p>
                    </div>
                  </div>
                ))
              }
            </div>
            <div className="pagination">
              <Pagination onChange={(page, pageSize) => this._onPaginationEvent(page, pageSize)} defaultCurrent={this.state.page} defaultPageSize={this.state.pageSize} total={articles.length} />
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default withRouter(Article);
