
import $ from 'jquery'
setTimeout(() => {
// ============================ 公共头部 ==================================== //

    $(window).scroll(function () {
        $('.fuck.animated').each(function (k,v) {
            if(($(window).scrollTop()> ($(v).offset().top)) ) {
                let animateCss = $(v).attr('type');
                $(v).removeClass(animateCss);
                $(v).addClass(animateCss);
                $(v).addClass('in');
            }
        });
        if($('.access-bg').length !== 0) {
            if(($(window).scrollTop() > ($('.access-bg').offset().top)-700)) {
                $('.access-bg').removeClass('earth');
                $('.access-bg').addClass('earth')
            }
        }

    });
},0)
