import React, { Component, useState, useEffect } from "react";
import {Link} from 'react-router-dom';
import "../../assets/css/global.scss";
import HeadList from './components/5g-overView/index';
import classnames from 'classnames';
import cookie from '../../assets/libs/cookie';
import $ from 'jquery'

let userInfoValue = JSON.parse(cookie.get('userInfoValue')) || '';
function Header(props) {
  const [showElem, setShowElem] = useState(true);
  useEffect(() =>{
    let navServTimer = -1;
    let headerTimer = null;
    let a = document.getElementById('nav-header-serv');
    a && a.addEventListener('mouseover', function(e) {
          clearTimeout(headerTimer);
          if (navServTimer !== -1) {
            clearTimeout(navServTimer);
            navServTimer = -1;
          }
          a.classList.add("active");
          document.querySelector('.cat-box').classList.add('active');
          document.querySelector('.mod-header').classList.add('hover');
        },
        // mouse leave
        a && a.addEventListener('mouseout', function(e) {
          // 延迟200毫秒
          navServTimer = setTimeout(() => {
            navServTimer = -1;
            document.querySelector('.cat-box').classList.remove('active');
            a.classList.remove('active');
          }, 200);
          headerTimer = setTimeout(() => {
            document.querySelector('.mod-header').classList.remove('hover');
          }, 500);
        })
    );
    window.addEventListener('scroll',()=>{
      let bodyTop = document.body.scrollTop;
      let htmlTop = document.documentElement.scrollTop;
      if (bodyTop>0||htmlTop>0){
        document.querySelector('.mod-header').classList.add('mod-header-fixed');
      }else {
        document.querySelector('.mod-header').classList.remove('mod-header-fixed');
      }
    });
    document.querySelector('.mod-header').onmouseover = ()=>{
      $('.mod-header').each((k,v)=>{
        $(v).addClass('header-hover')
      })
    };
    document.querySelector('.mod-header').onmouseout = ()=>{
      $('.mod-header').each((k,v)=>{
        $(v).removeClass('header-hover')
      })
    };
    const handleMount = () => {
  if(userInfoValue) {
    setShowElem(false);
  }
  };
  handleMount();
  },[]);

  const handleClick = () => {
    cookie.del('userInfoValue');
    cookie.del('userInfo');
    cookie.del('userInfoValue', '/', '5g.sparta.html5.qq.com','-1');
    cookie.del('userInfo', '/', '5g.sparta.html5.qq.com', '-1');
    window.location.reload();
    // document.cookie = "username=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
  };
    return (
      <div
        className={classnames(`mod-header head-cr ${props.className ||  ''}`)}
        style={{ height: props.height || '4.05vw'}}
      >
        <HeadList/>
        <div className="layout xxx clearfix">
            <Link to="/" className="mod-header-logo" _stat_click_id="header_logo"></Link>
          <div className={showElem ? "mod-header-nav" : 'mod-header-nav userRig'}>
            <ul id="jmod-header-nav">
              <li
                  className="top-cat hassub"
              >
              </li>
              <li id="nav-header-serv" className="top-cat hassub" data-cat="capability"
              >
                <h2>
                  <Link   className={window.location.pathname.indexOf('ability') !== -1 ? 'active-route' : undefined}>5G技术能力</Link>
                </h2>
              </li>
              <li
                id="nav-header-case"
                className="top-cat hassub"
                data-cat="case"
              >
                <h2>
                  <Link to="/industry"   className={window.location.pathname === '/industry' ? 'active-route' : undefined}>5G行业应用</Link>
                </h2>
              </li>
              <li id="nav-header-info" className="top-cat">
                <h2>
                  <Link to="/ecology"   className={window.location.pathname === '/ecology' ? 'active-route' : undefined} _stat_click_id="header_info">
                    5G生态计划
                  </Link>
                </h2>
              </li>
              <li id="nav-header-accelerator" className="top-cat">
                <h2>
                  <Link

                      to="/deveFile"
                      className={window.location.pathname === '/deveFile' || window.location.pathname.indexOf('fileDetail') !== -1 ? 'active-route' : undefined}
                    _stat_click_id="header_accelerator"
                  >
                    开发文档
                  </Link>
                </h2>
              </li>
              {/* <li id="nav-header-hr" className="top-cat">
                <h2>
                  <a href="/ctiyPlan" _stat_click_id="header_hr">
                    5G城市计划
                  </a>
                </h2>
              </li>*/}
              <li id="nav-header-join" className="top-cat">
                <h2>
                  <Link  to="/consultation"  className={window.location.pathname === '/consultation' ? 'active-route' : undefined} _stat_click_id="header_joinus">
                    最新资讯
                  </Link>
                </h2>
              </li>
            </ul>
            <i className="jmod-nav-slider slider hidden" style={{transform: "translateX(0px)"}}></i>
          </div>

          <div className="mod-header-user">
            <Link

                to="/login"
              target="_blank"
              className={showElem ? 'try jmod-header-try' : 'try jmod-header-try hide'}
              _stat_click_id="header_console"
            >
              控制台
            </Link>
            <Link

                to="/5g-admin"
              target="_blank"
              className={showElem ? 'console jmod-header-console hidden' : 'console jmod-header-console'}
              _stat_click_id="header_console"
            >
              控制台
            </Link>
            <div className={showElem ? 'jmod-header-user user  hidden' : 'jmod-header-user user'}>
            <em className="jmod-header-name">{userInfoValue.name || 12345678}</em>
              <i className="icon-btom"></i>
              <div className="dropdown">
                <Link
                    to="/5g-admin/mine/useraccount"
                  target="__blank"
                  _stat_click_id="header_userinfo"
                >
                  帐号信息
                </Link>
                <Link   to="" _stat_click_id="header_logout" onClick={handleClick}>
                  退出
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

export default Header;
